#App {
  margin: 40px;
}

table.data-grid {
  width: 100%;
}

.data-grid-container .data-grid .cell.rds-chart-name {
  background-color: rgba(0, 0, 0, 0.42);
  color: rgba(0, 0, 0, 1);
}

.chart-type-selector {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 40px;
  margin-bottom: 40px;
}

.chart-type-selector > div {
  margin: 10px;
}

.download-btn-wrapper {
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
}

.feedback-link-wrapper {
  margin-top: 20px;
  text-align: right;
}

.save-buttons {
  display: flex;
  justify-content: space-around;
}
